import { Component } from "react";
import { Link } from "react-router-dom";
class Footer extends Component{
    render(){
        return(
            <footer>
                <div className="footer-dashboard">
                    <Link to="/spacestagram" className="footer-icon home"></Link>
                    <Link to="/spacestagram/search" className="footer-icon search"></Link>
                    <Link to="/spacestagram/info" className="footer-icon info"></Link>
                </div>
          </footer>
        )
    }
}

export default Footer